.sidebar {
    flex: 0.35;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: aquamarine;
    border-right: 3px solid blue;
}

.sidebar__header {
    display: flex;
    align-items: center;
    padding: 10px;
    height: 50px;
}

.sidebar__avatar {
    cursor: pointer;
    margin: 10px;
}

.sidebar__input {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    background-color: lavender;
    color: gray;
    border-radius: 5px;
}

.sidebar__input > input {
    border: none;
    background: transparent;
    outline-width: 0;
}

.sidebar__chats {
    overflow: scroll;
}

.sidebar__chats::-webkit-scrollbar {
    display: none;
}

.sidebar__chats {
    -ms-overflow-style: none;
    scrollbar-width: none;
}